#header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5; }
  #header button {
    outline: none;
    border: none;
    background-color: transparent;
    padding: 0; }

.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 35px 0 20px 0;
  border-bottom: 4px solid #F8E4AE; }
  @media (max-width: 1023px) {
    .header__content {
      padding: 35px 30px 20px 30px; }
      .active .header__content::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: #F8E4AE; } }
  @media (max-width: 479px) {
    .header__content {
      padding: 35px 10px 20px 10px; } }

.header__main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-right: 120px; }
  @media (max-width: 479px) {
    .header__main {
      width: auto;
      margin: 0;
      order: 2; } }

.header__logo {
  z-index: 1; }
  .header__logo svg {
    width: 53px;
    height: 46px;
    fill: #F8E4AE; }
    .active .header__logo svg {
      fill: #000; }

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  @media (max-width: 1024px) {
    .menu {
      position: fixed;
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      top: -100%;
      left: 0;
      transition: all .5s ease 0s;
      background-color: #F8E4AE;
      padding: 120px 15px 30px 15px;
      overflow: auto;
      z-index: -1; } }

@media (max-width: 1024px) {
  .menu_active {
    top: 0; } }

.menu__list {
  padding: 0;
  margin: 0;
  list-style: none; }
  @media (min-width: 1025px) {
    .menu__list {
      display: flex;
      justify-content: space-evenly; } }
  @media (max-width: 1023px) {
    .menu__list {
      margin-bottom: 30px; } }

.menu__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 20px;
  height: 100%;
  position: relative; }

.menu__link {
  font-size: 20px;
  color: #fff;
  transition: color .3s ease 0s; }
  @media (max-width: 1023px) {
    .menu__link {
      color: #000; } }

.menu__social {
  position: relative;
  display: none;
  justify-content: space-between; }
  @media (max-width: 1023px) {
    .menu__social {
      display: flex;
      min-width: 350px; } }
  @media (max-width: 479px) {
    .menu__social {
      min-width: 250px; } }
  @media (max-width: 479px) {
    .menu__social img {
      width: 28px;
      height: 28px; } }

.header__phone {
  position: relative;
  max-width: 34px;
  min-width: 34px;
  max-height: 34px;
  min-height: 34px; }
  @media (max-width: 1024px) {
    .header__phone {
      margin-right: 55px; }
      .active .header__phone {
        display: none; } }
  @media (max-width: 479px) {
    .header__phone {
      margin: 0;
      order: 1; } }
  .header__phone a {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0; }
  .header__phone svg {
    width: 34px;
    height: 34px;
    fill: #F8E4AE; }

/* ---------------burger */
.burger {
  display: none;
  position: relative;
  max-width: 30px;
  min-width: 30px;
  max-height: 20px;
  min-height: 20px; }
  @media (max-width: 1024px) {
    .burger {
      display: block; } }
  @media (max-width: 479px) {
    .burger {
      order: 3; } }
  .burger span {
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #F8E4AE;
    top: 50%;
    right: 0;
    border-radius: 10px;
    transition: all .3s ease 0s; }
    .burger span:first-child {
      top: 0; }
    .burger span:last-child {
      top: 100%;
      width: 50%; }

.burger_active span {
  background: #000;
  opacity: 0; }
  .burger_active span:first-child {
    opacity: 1;
    transform: rotate(-45deg);
    top: 6px; }
  .burger_active span:last-child {
    opacity: 1;
    transform: rotate(45deg);
    top: 6px;
    width: 100%; }

